@import "reset";
@import "fonts";
@import "mixins";
@import "vars";
@import "generated/sprite-svg";
:root {
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
  }
}

body,
html {
  width: 100%;
  height: 100%;
}

.wrapper {
  width: 100%;
  height: 100%;
  font-family: $font-primary;
  .icon {
    transition: all .2s ease;
  }
  a {
    transition: all 0.2s ease;
    color: inherit;
    &:hover {
      text-decoration: none;
    }
  }
}

.bold-text {
  font-weight: bold;
}

.loginpage {
  position: relative;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  padding-left: 5%;
  padding-right: 5%;
  background-color: #f2f2f2;
  @media only screen and (max-width: 580px) {
    padding-left: 2%;
    padding-right: 2%;
  }
}

// header
.header {
  padding-top: 20px;
  display: flex;
  align-items: center;
  z-index: 10;
  color: #3a3c44;
  @media only screen and (max-width: 768px) {
    padding-top: 10px;
    flex-direction: column;
    color: #eee;
  }
  @media only screen and (max-width: 480px) {
    padding-bottom: 40px;
    order: 3;
    text-align: center;
    .header__socials {
      display: none;
    }
  }
  .header__left {
    @media only screen and (max-width: 768px) {
      margin-top: 20px;
      order: 2;
    }
  }
  .header__logo {
    width: 220px;
    img {
      min-width: 100px;
      width: 100%;
    }
    @media only screen and (max-width: 768px) {
      width: 144px;
      height: 32px;
    }
  }
  .header__title {
    margin-top: 8px;
    padding-left: 10px;
    font-size: 13.1px;
  }
  .header__socials {
    display: flex;
    margin-left: auto;
    flex-wrap: wrap;
    @media only screen and (max-width: 768px) {
      order: 1;
    }
  }
  .header__icon {
    margin-left: 28px;
    margin-bottom: 5px;
    .icon {
      fill: #fff;
      @media only screen and (max-width: 768px) {
        fill: #999999;
        font-size: 12px;
      }
      @media only screen and (max-width: 480px) {}
      &:hover {
        fill: #9caa49;
      }
    }
    @media only screen and (max-width: 768px) {
      margin-left: 15px;
    }
  }
}

.bg-foto {
  position: absolute;
  top: 0;
  right: 0;
  width: 50%;
  height: 100%;
  background-image: url(../img/bg-foto.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 1;
  @media only screen and (max-width: 768px) {
    width: 100%;
  }
  @media only screen and (max-width: 480px) {
    background-image: url(../img/bg-foto-mobile.jpg);
  }
}

// main content
.loginpage__content {
  flex: 1 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  margin-bottom: 50px;
  z-index: 10;
  .content {
    width: 100%;
    padding-left: 6%;
    padding-bottom: 12px;
    @media only screen and (max-width: 1200px) {
      padding-left: 0;
    }
    @media only screen and (max-width: 768px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }
  .content__title {
    margin-top: 35px;
    margin-bottom: 40px;
    @include rfs(30px);
    font-weight: 300;
    text-transform: uppercase;
    color: #393b44;
    @media only screen and (max-width: 768px) {
      color: #fff;
    }
  }
  .content__icon {
    @include rfs(30px);
    width: 3em;
    height: 3em;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #5c5f69;
    border-radius: 50%;
    .icon {
      height: 1.2em;
      width: 1em;
    }
    .icon-reports-light {
      display: none;
    }
    @media only screen and (max-width: 768px) {
      margin: 0 auto;
      border-color: #b1b2b3;
      .icon-reports {
        display: none;
      }
      .icon-reports-light {
        display: flex;
        height: 1.2em;
        width: 1em;
      }
    }
  }
  .form__title {
    margin-bottom: 19px;
    @include rfs(17px);
    font-weight: 500;
    color: #3a3c44;
    @media only screen and (max-width: 768px) {
      color: #fff;
    }
  }
  .content__form {
    width: 100%;
    max-width: 390px;
  }
  .form__row {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    @media only screen and (max-width: 768px) {
      flex-direction: column;
    }
  }
  input {
    height: 2.8em;
    margin-bottom: 10px;
    @include rfs(14px);
    padding-left: 12px;
    color: #3a3c44;
    font-family: inherit;
    border: 2px solid #9caa49;
    border-radius: 3px;
    background-color: transparent;
    @media only screen and (max-width: 768px) {
      max-width: 234px;
      width: 100%;
      padding: 0;
      text-align: center;
      color: #5c5f69;
      background-color: #eeeeee;
      border-color: transparent;
    }
    @media only screen and (max-width: 480px) {
      background-color: #eeeeee;
    }
  }
  .form__password {
    margin-left: 12px;
    @media only screen and (max-width: 768px) {
      margin-top: 7px;
      margin-left: 0;
    }
  }
  .form__forgot {
    margin-bottom: 45px;
    color: #999999;
    font-size: 14px;
    font-weight: 500;
    color: #999999;
    &:hover {
      color: darken(#999999, 10%);
    }
    @media only screen and (max-width: 768px) {
      color: #fff;
      &:hover {
        color: darken(#ffffff, 10%);
      }
    }
  }
  .form__submit {
    height: 46px;
    padding-left: 50px;
    padding-right: 50px;
    @include rfs(15.3px);
    font-family: inherit;
    font-weight: 700;
    text-transform: uppercase;
    color: #fff;
    border: none;
    background-color: #9caa49;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
    box-sizing: border-box;
    &:hover {
      cursor: pointer;
      background-color: lighten(#9caa49, 5%);
    }
  }
  .form__notify:focus,
  input:focus {
    outline: none;
    border: 2px solid lighten(#9caa49, 20%);
  }
  input::placeholder {
    color: #999999;
    font-weight: 600;
    font-size: 15px;
    font-family: inherit;
  }
}

// footer
.footer {
  flex: 0 0 auto;
  padding-bottom: 15px;
  z-index: 10;
  @media only screen and (max-width: 480px) {
    display: none;
  }
}

.footer__copy {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 13px;
  color: #fff;
  font-weight: 500;
  @media only screen and (max-width: 768px) {
    justify-content: center;
  }
}